.projects {
	padding-top: 60px;
	&__container {
	}

	&__promo {
		max-width: 460px;
		margin: 0 auto;
		margin-bottom: 60px;
		padding: 0 10px;

		text-align: center;
	}

	&__title {
		margin-bottom: 40px;
	}

	&__works {
		display: flex;
		width: 100%;
		align-items: stretch;
	}

	&__work:nth-child(3n + 1) {
		flex: 0 0 50%;
	}
	&__work:nth-child(3n + 2) {
		flex: 0 0 25%;
	}
	&__work:nth-child(3n + 3) {
		flex: 0 0 25%;
	}
	&__work {
		position: relative;
		z-index: 1;

		display: inline-block;
		&::after {
			content: "";
			position: absolute;
			z-index: 2;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;

			background-color: rgba(0, 0, 0, 0.7);

			transition: background-color 0.2s ease-in;
		}
		&:hover {
			&::after {
				background-color: rgba(0, 0, 0, 0);
			}
		}
		&:hover .projects__work-title {
			opacity: 0;
			visibility: hidden;
		}
		&:hover .projects__work-img {
			filter: none;
		}
	}
	&__work-img {
		width: 100%;
		height: 320px;
		object-fit: cover;
		filter: grayscale(100%);
		transition: filter 0.2s ease-in;
	}
	&__work-title {
		position: absolute;
		top: 50%;
		left: 50%;
		visibility: visible;
		z-index: 3;
		opacity: 1;

		color: #ffffff;

		font-family: $ffSS;
		font-size: 20px;
		text-transform: uppercase;

		transform: translate(-50%, -50%);
		transition: all 0.2s ease-in;
	}
	@media (max-width: $md2+px) {
		&__work-img {
			height: 250px;
		}
	}
	@media (max-width: $md3+px) {
		&__works {
			flex-wrap: wrap;
		}

		&__work:nth-child(3n + 1) {
			flex: 0 0 100%;
		}
		&__work:nth-child(3n + 2) {
			flex: 0 0 50%;
		}
		&__work:nth-child(3n + 3) {
			flex: 0 0 50%;
		}
	}
	@media (max-width: $md4+px) {
		&__work-img {
			height: 200px;
		}
		&__work:nth-child(3n + 1) {
			flex: 0 0 100%;
		}
		&__work:nth-child(3n + 2) {
			flex: 0 0 100%;
		}
		&__work:nth-child(3n + 3) {
			flex: 0 0 100%;
		}
	}
}
.footer-project {
	position: relative;

	padding-top: 38px;
	padding-bottom: 38px;
	background: url(../img/projects/bg.jpeg) center/cover no-repeat;
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		background-color: $accent-color;
		opacity: 0.7;
	}
	&__container {
		position: relative;
		z-index: 2;

		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__text {
		color: #ffffff;
		margin-right: 20px;

		text-align: justify;
		font-family: $ffOS;
		font-size: 20px;
	}
	&__link-wrapp {
		flex-grow: 1;
		flex-shrink: 0;
		display: flex;
		justify-content: flex-end;
	}
	&__link {
		color: #ffffff;
		border: 1px solid #ffffff;
		&:hover {
			color: $accent-color;
			background-color: #fff;
		}
	}
	@media (max-width: $md2+px) {
		&__text {
			font-size: 16px;
		}
	}
	@media (max-width: $md4+px) {
		&__text {
			text-align: center;
			margin-bottom: 20px;
			margin-right: 0px;
		}
		&__container {
			flex-direction: column;
		}
	}
}
