.details {
	background-color: #f3f3f3;
	padding-top: 60px;
	padding-bottom: 80px;
	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__promo {
		text-align: center;
	}

	&__title {
		margin-bottom: 40px;

		text-transform: uppercase;
	}

	&__text {
		max-width: 470px;
		margin-bottom: 60px;
	}

	&__icons {
		margin-bottom: 60px;
	}
	&__icon {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background-color: #81868e;

		width: 54px;
		height: 54px;

		transition: all 0.2s ease;
		&:not(:last-child) {
			margin-right: 25px;
		}
		&:hover {
			background-color: #19bd9a;
		}
	}
	&__icon-img {
		width: 30px;
		height: 30px;
	}
	@media (max-width: $md3+px) {
		padding-bottom: 60px;
		&__text {
			margin-bottom: 30px;
		}
		&__icons {
			margin-bottom: 40px;
		}
	}
}

.ideas {
	&__subtitle {
		margin-bottom: 40px;
	}

	&__text {
		display: flex;

		text-align: justify;
		p:first-child {
			margin-right: 130px;
		}
	}
	@media (max-width: $md2+px) {
		&__text {
			p {
				flex: 0 0 50%;
			}
			p:first-child {
				padding-right: 50px;
				margin-right: 0px;
			}
		}
	}
	@media (max-width: $md3+px) {
		&__subtitle {
			margin-bottom: 20px;
		}
		&__text {
			flex-direction: column;
			p {
				flex: 0 0 50%;
			}
			p:first-child {
				padding-right: 0px;
				margin-bottom: 30px;
			}
		}
	}
}
