.promo {
	&__container {
		display: flex;
		align-items: stretch;
		justify-content: space-between;
		max-width: 850px;
		padding-top: 80px;
		padding-bottom: 50px;
	}

	&__info {
		text-align: justify;
		margin-right: 50px;
	}

	&__title {
		padding-bottom: 50px;
	}

	&__text {
		padding-bottom: 30px;
		line-height: 60 / 30 * 100%;
	}

	&__link {
	}

	&__image {
		flex: 0 0 200px;
	}

	&__img {
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
	@media (max-width: $md4+px) {
		&__container {
			flex-direction: column-reverse;
		}
		&__info {
			margin-right: 0;
		}
		&__title {
			padding-top: 20px;
			padding-bottom: 15px;
		}
		&__text {
			padding-bottom: 10px;
		}
		&__image {
			align-self: center;
		}
		&__img {
			width: 180px;
			max-height: 100%;
			object-fit: cover;
		}
	}
}
