.header {
	position: relative;
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background: url(../img/header/header-bg.jpeg) center/cover no-repeat;
	&::after {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.404);
		z-index: 1;
	}

	&__info {
		position: relative;
		z-index: 2;

		text-align: center;
	}

	&__title {
		color: #fff;

		font-family: $ffSS;
		font-weight: 300;
		font-size: 35px;
		letter-spacing: 5px;
		text-transform: uppercase;
		margin-bottom: 40px;
	}

	&__promo {
		max-width: 600px;
		color: #fff;
		padding-bottom: 60px;

		font-family: $ffOS;
		font-size: 16px;
		line-height: 1.2;
	}

	&__scroll-link {
		position: absolute;
		z-index: 3;
		bottom: 70px;
		left: 50%;

		display: flex;
		justify-content: center;
		align-items: center;

		width: 37px;
		height: 37px;
		border-radius: 50%;

		background-color: rgb(10, 133, 106);

		transform: translateX(-50%);

		-webkit-animation: hoverWave linear 2.5s infinite;
		animation: hoverWave linear 2.5s infinite;

		transition: all 0.3s ease;
		&:hover {
			animation: hoverWaveBigger linear 2s infinite;
		}
	}
	@media (max-height: 320px) {
		&__container {
			padding-top: 100px;
		}
	}
	@media (max-height: $md4+px) {
		&__scroll-link {
			bottom: 20px;
		}
	}
}
@-webkit-keyframes hoverWave {
	0% {
		box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
	}
	40% {
		box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 15px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
	}
	80% {
		box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 26.7px rgba(25, 189, 154, 0.067);
	}
	100% {
		box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 40px rgba(25, 189, 154, 0);
	}
}
@keyframes hoverWave {
	0% {
		box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
	}
	40% {
		box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 15px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
	}
	80% {
		box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 26.7px rgba(25, 189, 154, 0.067);
	}
	100% {
		box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 40px rgba(25, 189, 154, 0);
	}
}
@-webkit-keyframes hoverWaveBigger {
	0% {
		box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
	}
	40% {
		box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 50px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
	}
	80% {
		box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 60px rgba(25, 189, 154, 0), 0 0 0 66.7px rgba(25, 189, 154, 0.067);
	}
	100% {
		box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 65px rgba(25, 189, 154, 0), 0 0 0 80px rgba(25, 189, 154, 0);
	}
}
@keyframes hoverWaveBigger {
	0% {
		box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
	}
	40% {
		box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 50px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
	}
	80% {
		box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 60px rgba(25, 189, 154, 0), 0 0 0 66.7px rgba(25, 189, 154, 0.067);
	}
	100% {
		box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 65px rgba(25, 189, 154, 0), 0 0 0 80px rgba(25, 189, 154, 0);
	}
}
.nav._active {
	&::before {
		background-color: rgb(124, 112, 58);
	}
}
.nav._active .nav__body {
	height: 60px;
}
.nav {
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: transparent;
		z-index: 2;
		transition: all 0.2s ease;
	}
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 50;
	transition: all 0.2s ease;
	&__body {
		position: relative;
		display: flex;
		justify-content: space-between;
		height: 114px;
		align-items: center;
		transition: all 0.2s ease;
	}
	&__logo {
		position: relative;
		z-index: 3;

		padding-bottom: 10px;
		text-transform: uppercase;
	}
	&__burger {
		display: none;
	}

	&__list {
		display: flex;
		li {
			position: relative;
			z-index: 2;
			&:not(:last-child) {
				margin-right: 20px;
			}
		}
	}
	&__link {
		display: inline-block;
		color: #fff;
		padding: 5px 15px;
		border: 1px solid transparent;
		border-radius: 15px;

		font-family: $ffOS;
		font-size: 16px;

		transition: all 0.2s ease;
		&:hover {
			border: 1px solid #fff;
		}
	}

	@media (max-width: $md2+px) {
		&__list {
			li {
				&:not(:last-child) {
					margin-right: 5px;
				}
			}
		}
	}
	@media (max-width: $md3+px) {
		&.active::before {
			background-color: rgb(124, 87, 63);
		}
		&__body {
			height: 50px;
		}
		&__logo {
			padding-top: 5px;
			padding-bottom: 0;
		}
		&__burger {
			display: block;
			position: relative;
			width: 30px;
			height: 20px;
			z-index: 3;
			position: relative;
		}
		&__burger span {
			position: absolute;
			width: 100%;
			height: 2px;
			background-color: #fff;
			left: 0;
			top: 9px;
			transition: all 0.3s ease 0s;
		}
		&__burger::before,
		&__burger::after {
			content: "";
			position: absolute;
			width: 100%;
			height: 2px;
			background-color: #fff;
			left: 0;
			transition: all 0.3s ease 0s;
		}
		&__burger::before {
			top: 0;
		}
		&__burger::after {
			bottom: 0;
		}
		&__burger.active span {
			transform: scale(0);
		}
		&__burger.active::before {
			transform: rotate(45deg);
			top: 9px;
		}
		&__burger.active::after {
			transform: rotate(-45deg);
			bottom: 9px;
		}
		&__menu {
			position: fixed;
			top: -100%;
			left: 0;
			overflow: auto;

			padding: 70px 10px 20px 10px;
			width: 100%;
			height: 100%;

			background: rgb(75, 79, 20);
			background: -moz-linear-gradient(90deg, rgba(75, 79, 20, 1) 0%, rgba(136, 90, 5, 1) 64%);
			background: -webkit-linear-gradient(90deg, rgba(75, 79, 20, 1) 0%, rgba(136, 90, 5, 1) 64%);
			background: linear-gradient(90deg, rgba(75, 79, 20, 1) 0%, rgba(136, 90, 5, 1) 64%);
			filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#4b4f14",endColorstr="#885a05",GradientType=1);

			transition: all 0.3s ease 0s;
		}
		&__menu.active {
			top: 0;
		}

		&__list {
			display: block;
		}
		&__list li {
			margin: 0 0 20px 0px;
			a {
				font-size: 30px;
			}
		}
	}

	@media (max-height: $md4+px) and (orientation: landscape) {
		&__menu {
			padding: 10px 20px 10px 20px;
		}
		&__logo {
			padding-top: 5px;
			padding-bottom: 0;
		}
		&__body {
			height: 60px;
		}

		&__list li {
			&:not(:last-child) {
				margin-right: 0;
			}
			a {
				font-size: 15px;
			}
		}
	}
}
.nav.nav--active {
	&::before {
		background: rgb(75, 79, 20);
		background: -moz-linear-gradient(90deg, rgba(75, 79, 20, 1) 0%, rgba(136, 90, 5, 1) 64%);
		background: -webkit-linear-gradient(90deg, rgba(75, 79, 20, 1) 0%, rgba(136, 90, 5, 1) 64%);
		background: linear-gradient(90deg, rgba(75, 79, 20, 1) 0%, rgba(136, 90, 5, 1) 64%);
		filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#4b4f14",endColorstr="#885a05",GradientType=1);
	}
}
.nav.nav--active .nav__body {
	height: 50px;
}
