// Файл обнуления стилей
@import "_nullstyle.scss";
//  //

// Mixins
@import "mixins";
//  //

// Подключение шрифтов
@import "fonts";

@import url("https://fonts.googleapis.com/css?family=Open+Sans:regular,700");
@import url("https://fonts.googleapis.com/css?family=Roboto:300");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300italic,regular,900");
//  //

// Переменные
@import "variables";
//  //

// Шаблоны
%scroll-del {
	&::-webkit-scrollbar {
		// chrome based
		width: 0px; // ширина scrollbar'a
		background: transparent; // опционально
	}
	-ms-overflow-style: none; // IE 10+
	scrollbar-width: none; // Firefox
}
// @extend %scroll-del
//  //

html {
	min-height: 100%;
	margin: 0;
	padding: 0;
	scroll-behavior: smooth;
}
body {
	position: relative;
	min-height: 100%;
	margin: 0;
	padding: 0;

	font-family: $ffOS;
}
.wrapper {
	height: 100%;
}

.lock {
	overflow: hidden;
}

.container {
	max-width: $maxWidthContainer + 30px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
}

@media (max-width: $md2+px) and (orientation: portrait) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
}

@media (max-width: $md3+px) and (orientation: portrait) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
}

@media (max-height: $md4+px) and (orientation: landscape) {
	.container {
		padding-left: 50px;
		padding-right: 50px;
	}
}
@import "common";
@import "header";
@import "promo";
@import "details";
@import "projects";
